import React from "react";
import "./style.css";
import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { Col, Row, Container } from "../Grid";
import Jumbotron from "../Jumbotron";
import API from "../../utils/API";

import { Button } from "react-bootstrap";
import DeleteBtn from "../DeleteBtn";
import SaveBtn from "../SaveBtn";

// This file exports both the List and ListItem components

export function List({ children }) {
     return (
          <div className="list-overflow-container">
               <ul className="list-group">{children}</ul>
          </div>
     );
}

// component to render each book
export function ListItem(props) {
     // function to handle saving book to db when save button is clicked
     const handleSaveBtn = (event) => {
          API.saveBook({
               title: props.title,
               authors: props.authors,
               description: props.description,
               image: props.image,
               link: props.link,
          })
               .then((res) => console.log(res))
               .catch((err) => console.log(err));
     };

     // function to handle deleting book from db when delete button is clicked
     const handleDeleteBtn = (event) => {
          API.deleteBook(props.id)
               .then((res) => {
                    // use loadBooks prop from Saved page component
                    props.loadBooks();
                    console.log(props.id);
               })
               .catch((err) => console.log(err));
     };

     return (
          <li className="list-group-item" key={props.id}>
               <Container>
                    <Row>
                         <Col size="xs-4 sm-2 md-3">
                              <img src={props.image} alt=" No Image Found" />
                         </Col>
                         <Col size="xs-8 sm-10 md-9">
                              <h3>{props.title}</h3>
                              <p>
                                   Written By{" "}
                                   {[props.authors].flat().join(", ")}
                              </p>
                              <p>{props.description}</p>
                              <a
                                   rel="noreferrer noopener"
                                   className="btn btn-lg btn-primary input-lg view"
                                   target="_blank"
                                   href={props.link}
                                   style={{
                                        backgroundColor: "lightBlue",
                                        borderColor: "lightblue",
                                   }}
                              >
                                   View
                              </a>
                              {/* if there is an object id render the SaveBtn component else render the DeleteBtn component */}
                              {!props.id ? (
                                   <Button
                                        type="success"
                                        className="input-lg"
                                        onClick={handleSaveBtn}
                                        style={{
                                             backgroundColor: "green",
                                             marginLeft: "10px",
                                             borderColor: "green",
                                        }}
                                   >
                                        Save
                                   </Button>
                              ) : (
                                   <Button
                                        type="danger"
                                        className="input-lg"
                                        onClick={handleDeleteBtn}
                                        style={{
                                             marginLeft: "10px",
                                             borderColor: "red",
                                        }}
                                   >
                                        Delete
                                   </Button>
                              )}
                         </Col>
                    </Row>
               </Container>
          </li>
     );
}
